import React from "react"

export const List = ({variant, items})=>{

    // console.log(variant)
    const ListComponent = variant ==='ordered'?'ol':'ul';
    return (
        <div>
           
            <ListComponent className="list-disc pl-4">
                {items.map((item,index)=>{
                    // console.log(item)
                    <li className="font-bold text-black" key={index}>{item}</li>
                })}
             
            </ListComponent> 
        </div>
    )
}